/* ------------------------------------------------------------------------------------------------ */
/* Footer */
/* ------------------------------------------------------------------------------------------------ */
.footer {
  position: relative;
  z-index: 2;
  background: $primary;
  color: #FFF;
  font-size: 14px;
  line-height: 20px;
  border-radius: 30px 30px 0 0;
  p {
    color: #FFF;
    font-size: 14px;
    line-height: 20px; }
  a {
    color: #FFF;
    &:hover {
      color: #FFF; } }
  &__logo {
    img {
      display: block;
      height: 24px;
      width: auto; }
    span {
      display: block;
      &:last-child {
        margin-top: 12px; } }
    &:hover {
      text-decoration: none; } }
  &__title {
    color: #FFF;
    font-size: 14px;
    line-height: 20px;
    font-weight: 500;
    margin-bottom: 8px; }
  &__menu {
    li {
      display: block;
      padding: 3px 0; }
    a {
      display: block; } }
  &__social {
    margin-left: -6px;
    margin-right: -6px;
    padding-top: 10px;
    .item {
      display: inline-block;
      vertical-align: center;
      padding: 0 6px; }
    a {
      display: block;
      opacity: 1;
      transition: all .3s ease;
      &:hover {
        opacity: 0.5; } }
    img {
      display: block;
      max-height: 22px;
      width: auto; } }
  &__main {
    padding-top: 48px;
    padding-bottom: 48px; }
  &__bottom {
    padding: 16px 0 42px; } }
