/* ------------------------------------------------------------------------------------------------ */
/* General */
/* ------------------------------------------------------------------------------------------------ */

.btn {
  font-size: 16px;
  line-height: 24px;
  padding: 7px 24px;
  border-radius: 8px;
  font-weight: 400;
  transition: all .3s ease;
  &:hover {
    text-decoration: none; }
  &:focus, &.focus {
    outline: 0;
    box-shadow: none; }
  &.disabled,
  &:disabled {
    opacity: 0.5; }
  .icon {
    display: inline-block;
    width: 18px;
    height: 18px;
    &:first-child {
      margin-right: 8px; }
    &:last-child {
      margin-left: 8px; } }
  span {
    display: inline-block; }
  // BTN PRIMARY
  &-primary {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-primary:hover {
    color: #FFF;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%); }
  &-primary:focus, &-primary.focus {
    color: #fff;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%);
    box-shadow: none; }
  &-primary.disabled, &-primary:disabled {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-primary:not(:disabled):not(.disabled):active,
  &-primary:not(:disabled):not(.disabled).active,
  .show > &-primary.dropdown-toggle {
    color: #fff;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%); }
  &-primary:not(:disabled):not(.disabled).active:focus,
  &-primary:not(:disabled):not(.disabled):active:focus,
  .show > &-primary.dropdown-toggle:focus {
    box-shadow: none; }
  // BTN BLACK
  &-black {
    color: #FFF;
    background-color: #000;
    border-color: #000; }
  &-black:hover {
    color: #FFF;
    background-color: darken(#000, 10%);
    border-color: darken(#000, 10%); }
  &-black:focus, &-black.focus {
    color: #fff;
    background-color: darken(#000, 10%);
    border-color: darken(#000, 10%);
    box-shadow: none; }
  &-black.disabled, &-black:disabled {
    color: #FFF;
    background-color: #000;
    border-color: #000; }
  &-black:not(:disabled):not(.disabled):active,
  &-black:not(:disabled):not(.disabled).active,
  .show > &-black.dropdown-toggle {
    color: #fff;
    background-color: darken(#000, 10%);
    border-color: darken(#000, 10%); }
  &-black:not(:disabled):not(.disabled).active:focus,
  &-black:not(:disabled):not(.disabled):active:focus,
  .show > &-black.dropdown-toggle:focus {
    box-shadow: none; }
  // BTN BORDER
  &-border {
    color: #000;
    background-color: transparent;
    border-color: #000; }
  &-border:hover {
    color: #fff;
    background-color: #000;
    border-color: #000; }
  &-border:focus, &-border.focus {
    color: #fff;
    background-color: #000;
    border-color: #000;
    box-shadow: none; }
  &-border.disabled, &-border:disabled {
    color: #000;
    background-color: transparent;
    border-color: #000; }
  &-border:not(:disabled):not(.disabled):active,
  &-border:not(:disabled):not(.disabled).active,
  .show > &-border.dropdown-toggle {
    color: #fff;
    background-color: #000;
    border-color: #000; }
  &-border:not(:disabled):not(.disabled).active:focus,
  &-border:not(:disabled):not(.disabled):active:focus,
  .show > &-border.dropdown-toggle:focus {
    box-shadow: none; }
  // BTN BORDER KLIEN
  &-border-klien {
    color: $klien;
    background-color: transparent;
    border-color: $klien; }
  &-border-klien:hover {
    color: #fff;
    background-color: $klien;
    border-color: $klien; }
  &-border-klien:focus, &-border-klien.focus {
    color: #fff;
    background-color: $klien;
    border-color: $klien;
    box-shadow: none; }
  &-border-klien.disabled, &-border-klien:disabled {
    color: $klien;
    background-color: transparent;
    border-color: $klien; }
  &-border-klien:not(:disabled):not(.disabled):active,
  &-border-klien:not(:disabled):not(.disabled).active,
  .show > &-border-klien.dropdown-toggle {
    color: #fff;
    background-color: $klien;
    border-color: $klien; }
  &-border-klien:not(:disabled):not(.disabled).active:focus,
  &-border-klien:not(:disabled):not(.disabled):active:focus,
  .show > &-border-klien.dropdown-toggle:focus {
    box-shadow: none; }
  // BTN BORDER TALENTA
  &-border-talenta {
    color: $talenta;
    background-color: transparent;
    border-color: $talenta; }
  &-border-talenta:hover {
    color: #fff;
    background-color: $talenta;
    border-color: $talenta; }
  &-border-talenta:focus, &-border-talenta.focus {
    color: #fff;
    background-color: $talenta;
    border-color: $talenta;
    box-shadow: none; }
  &-border-talenta.disabled, &-border-talenta:disabled {
    color: $talenta;
    background-color: transparent;
    border-color: $talenta; }
  &-border-talenta:not(:disabled):not(.disabled):active,
  &-border-talenta:not(:disabled):not(.disabled).active,
  .show > &-border-talenta.dropdown-toggle {
    color: #fff;
    background-color: $talenta;
    border-color: $talenta; }
  &-border-talenta:not(:disabled):not(.disabled).active:focus,
  &-border-talenta:not(:disabled):not(.disabled):active:focus,
  .show > &-border-talenta.dropdown-toggle:focus {
    box-shadow: none; }
  // BTN BORDER WHITE
  &-border-white {
    color: #FFF;
    background-color: transparent;
    border-color: #FFF; }
  &-border-white:hover {
    color: #000;
    background-color: #FFF;
    border-color: #FFF; }
  &-border-white:focus, &-border-white.focus {
    color: #fff;
    background-color: #FFF;
    border-color: #FFF;
    box-shadow: none; }
  &-border-white.disabled, &-border-white:disabled {
    color: #FFF;
    background-color: transparent;
    border-color: #FFF; }
  &-border-white:not(:disabled):not(.disabled):active,
  &-border-white:not(:disabled):not(.disabled).active,
  .show > &-border-white.dropdown-toggle {
    color: #fff;
    background-color: #FFF;
    border-color: #FFF; }
  &-border-white:not(:disabled):not(.disabled).active:focus,
  &-border-white:not(:disabled):not(.disabled):active:focus,
  .show > &-border-white.dropdown-toggle:focus {
    box-shadow: none; } }

.btn-link-black {
  color: $text;
  text-decoration: underline;
  transition: all .3s ease;
  &:hover {
    color: $secondary; } }

.link-primary {
  color: $primary;
  transition: all .3s ease;
  &:hover {
    color: $primary;
    text-decoration: underline; } }

