/* ------------------------------------------------------------------------------------------------ */
/* Custom Plugins */
/* ------------------------------------------------------------------------------------------------ */

.owl-carousel {
  position: relative;
  * {
    outline: none !important; }
  .owl-nav {
    position: absolute;
    z-index: 2;
    width: 100%;
    top: 50%;
    span {
      display: block;
      padding: 6px;
      opacity: 1;
      border-radius: 21px;
      border: 1px solid transparent;
      transition: all .3s ease;
      img {
        display: block;
        width: 30px;
        height: 30px; }
      &:hover {
        border: 1px solid #f1f1f1;
        background: #FFF;
        box-shadow: 2px 2px 8px #00000020; } }
    button.disabled {
      opacity: 0.5;
      pointer-events: none; }
    button.owl-next {
      position: absolute;
      top: -21px;
      right: -54px; }
    button.owl-prev {
      position: absolute;
      top: -21px;
      left: -54px; } }
  .owl-dots {
    display: block;
    padding-top: 16px;
    font-size: 0;
    line-height: 0;
    text-align: center; }
  button.owl-dot {
    display: inline-block;
    span {
      display: block;
      width: 30px;
      height: 6px;
      background: #E6E6E6;
      border-radius: 3px;
      margin: 0 3px; }
    &.active span {
      background: #000; } } }
