/* ------------------------------------------------------------------------------------------------ */
/* Header */
/* ------------------------------------------------------------------------------------------------ */

.header {
  position: fixed;
  z-index: 200;
  display: block;
  width: 100%;
  left: 0;
  top: 0;
  padding: 20px 0;
  // background: rgba(255,255,255,0.7)
  background: #FFF;
  border-radius: 0 0 30px 30px;
  transition: all .3s ease-in-out;
  &__logo {
    img {
      display: block;
      width: auto;
      height: 24px;
      margin-top: 8px; } }
  &__menu {
    .main-menu {
      display: flex;
      width: 100%;
      justify-content: flex-end;
      align-items: center;
      &__item {
        display: block;
        position: relative;
        padding: 0 16px;
        &:first-child {
          padding-left: 0; }
        &:last-child {
          padding-right: 0; }
        &.has-sub {
          .main-menu__link {
            position: relative;
            padding-right: 18px;
            .svg {
              display: block;
              position: absolute;
              width: 12px;
              height: 12px;
              top: calc(50% - 6px);
              right: 0;
              transform: rotateX(0);
              transition: all .3s ease;
              path {
                transition: all .3s ease; } } }
          &:hover {
            .main-menu__link {
              color: $primary;
              .svg {
                transform: rotateX(180deg);
                path {
                  fill: $primary !important; } } }
            .sub-menu {
              transform: rotateX(0);
              opacity: 1;
              visibility: visible; } } } }
      &__link {
        display: block;
        color: #000000;
        font-size: 16px;
        line-height: 40px;
        transition: all .3s ease;
        &:hover {
          color: $primary;
          text-decoration: none; } }
      a.btn {
        min-width: 120px; } }
    .sub-menu {
      display: block;
      position: absolute;
      top: 100%;
      background: #FFF;
      box-shadow: 2px 2px 8px #00000014;
      padding: 15px 0;
      border: 1px solid #f1f1f1;
      border-radius: 10px;
      transform: rotateX(90deg);
      transform-origin: top;
      opacity: 0;
      visibility: hidden;
      transition: all .3s ease;
      &__item {}
      &__link {
        display: block;
        padding: 5px 15px;
        white-space: nowrap;
        color: $text;
        transition: all .3s ease;
        &:hover {
          color: $primary;
          text-decoration: none; } } } }
  &.sticky {
    padding: 8px 0;
    box-shadow: 2px 2px 8px #00000014; } }
