/* ------------------------------------------------------------------------------------------------ */
/* Content */
/* ------------------------------------------------------------------------------------------------ */

.masthead {
  padding: 80px 0;
  &__sub {
    display: block;
    margin-bottom: 0; }
  .masthead_title {
    margin-bottom: 32px; }
  &__action {
    .btn {
      font-weight: 500;
      min-width: 170px; } }
  &__img {
    margin-bottom: 48px; }
  &--home {
    .masthead__content {
      .masthead__sub {
        .alt-1 {
          display: block;
          color: $primary; }
        .alt-2 {
          display: none;
          color: $secondary; } }
      .masthead__title {
        position: relative;
        overflow: hidden;
        .alt-1 {
          display: block;
          position: relative;
          transform: translateY(0);
          transition: all .4s ease-in-out;
          span.hightlight {
            color: $primary; } }
        .alt-2 {
          position: absolute;
          top: 0;
          left: 0;
          transform: translateY(100%);
          transition: all .4s ease-in-out;
          span.hightlight {
            color: $secondary; } } }
      .masthead__action {
        .alt-1 {
          display: block; }
        .alt-2 {
          display: none; } }
      &.active {
        .masthead__sub {
          .alt-1 {
            display: none; }
          .alt-2 {
            display: block; } }
        .masthead__title {
          .alt-1 {
            transform: translateY(-100%); }
          .alt-2 {
            transform: translateY(0); } }
        .masthead__action {
          .alt-1 {
            display: none; }
          .alt-2 {
            display: block; } } } } }
  &--about {
    position: relative;
    &:before {
      content: "";
      display: block;
      width: 100%;
      height: 50%;
      position: absolute;
      bottom: 0;
      left: 0;
      background: #F6F6F6;
      border-radius: 30px 30px 0 0; } } }

.block-featured {
  padding-top: 80px;
  padding-bottom: 80px;
  &__img {
    margin-bottom: 32px;
    img {
      display: block;
      width: 100%;
      height: auto; } }
  &__title {
    color: $primary;
    margin-bottom: 24px;
    text-align: center; }
  &__action {
    text-align: center; }
  &__items {
    padding-top: 80px;
    .row {
      margin-left: -8px;
      margin-right: -8px; }
    .item {
      padding-left: 8px;
      padding-right: 8px;
      &__wrap {
        padding: 32px 48px;
        border-radius: 30px; }
      &__title {
        color: #FFF;
        font-size: 36px;
        line-height: 1.333em;
        font-weight: 500;
        margin-bottom: 0; }
      &__desc {
        font-size: 18px;
        line-height: 1.5em;
        color: #FFF; } } }
  &--home {
    padding-bottom: 160px; } }

.block-product {
  padding-top: 80px;
  padding-bottom: 80px;
  background: #2A3E65;
  &__title {
    color: #FFF;
    font-size: 30px;
    line-height: 1.5em;
    margin-bottom: 32px;
    text-align: center; }
  .block-item {
    color: #FFF;
    padding: 64px;
    border-radius: 30px;
    &+ .block-item {
      margin-top: 48px; }
    &__img {
      &:not(:last-child) {
        margin-bottom: 24px; }
      img {
        display: block;
        width: 100%;
        height: auto; } }
    &__img-title {
      font-size: 34px;
      line-height: 1.333em;
      color: #FFF;
      font-weight: 500;
      &:last-child {
        margin: 0; } }
    &__title {
      color: #FFF;
      font-size: 24px;
      line-height: 1.5em;
      &:last-child {
        margin: 0; }
      img {
        display: inline-block;
        vertical-align: middle; }
      span {
        display: inline-block;
        vertical-align: middle;
        color: #FFF;
        font-size: 32px;
        line-height: 32px;
        font-weight: 500;
        margin-left: 10px; } }
    p {
      margin-bottom: 32px;
      &:last-child {
        margin: 0; } } }
  &.bg-white {
    background: #FFF; }
  &--about {
    padding-top: 160px; } }

.block-clients {
  padding-top: 80px;
  padding-bottom: 80px;
  &__title {
    text-align: center;
    font-size: 24px;
    line-height: 1.5em;
    margin-bottom: 24px; }
  &__wrap {
    position: relative;
    width: 100%;
    overflow: hidden;
    .item {
      display: block;
      width: 100%;
      padding: 0 8px;
      &__wrap {
        padding: 20px;
        border-radius: 15px;
        background: #F6F6F6;
        height: 100px;
        display: flex;
        justify-content: center;
        align-items: center; }
      img {
        filter: grayscale(100);
        opacity: 0.5;
        display: block;
        max-height: 50px;
        width: auto;
        transition: all .4s ease-in-out; }
      &:hover {
        img {
          filter: grayscale(0);
          opacity: 1; } } } }
  .marquee__container {
    display: flex;
    width: 100vw; }
  &--home {
    padding-top: 160px; } }

.block-category {
  padding-top: 80px;
  padding-bottom: 80px;
  .items {
    margin-left: -8px;
    margin-right: -8px;
    .item {
      padding-left: 8px;
      padding-right: 8px;
      opacity: 1;
      &__wrap {
        position: relative;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        padding: 20px;
        margin-bottom: 16px;
        border-radius: 30px;
        &:after {
          z-index: 1;
          content: "";
          display: block;
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: #000;
          opacity: 0;
          transition: all .3s ease-in-out; }
        &:hover {
          text-decoration: none;
          &:after {
            opacity: 0.2; } } }
      &__title {
        position: relative;
        z-index: 2;
        color: #FFF;
        font-size: 24px;
        line-height: 1.5em;
        font-weight: 500;
        margin-bottom: 24px; }
      .icon {
        position: relative;
        z-index: 2;
        display: block;
        margin-top: auto;
        width: 40px;
        height: 40px; } } } }

.block-post {
  padding-top: 80px;
  padding-bottom: 80px;
  &__title {
    font-size: 24px;
    line-height: 1.5em;
    margin-bottom: 32px;
    text-align: center; }
  .post {
    &__img {
      position: relative;
      padding-top: 78%;
      overflow: hidden;
      border-radius: 30px 30px 0 0;
      img {
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        object-fit: cover;
        object-position: 50% 50%; } }
    &__content {
      padding: 20px 20px 30px;
      background: #F0F0F0;
      border-radius: 0 0 30px 30px; }
    &__title {
      font-size: 16px;
      line-height: 1.5em;
      font-weight: 500;
      margin-bottom: 12px;
      a {
        color: $text;
        opacity: 1;
        transition: all .3s ease;
        &:hover {
          opacity: 0.7;
          text-decoration: none; } } }
    &__desc {
      p {
        font-size: 12px;
        line-height: 1.5em; } }
    &__action {
      padding-top: 32px;
      a {
        font-size: 12px;
        line-height: 20px;
        color: $text;
        text-decoration: underline;
        opacity: 1;
        transition: all .3s ease;
        .arrow {
          display: inline-block;
          margin-left: 2px;
          width: 16px;
          height: 16px; }
        &:hover {
          opacity: 0.7; } } } } }

.block-ap {
  padding-top: 80px;
  padding-bottom: 80px;
  .item {
    text-align: center;
    &__img {
      text-align: center;
      margin-bottom: 64px;
      img {
        display: inline-block;
        width: auto;
        height: 340px; } }
    &__action {
      padding-top: 16px; } }
  &--about {
    padding-top: 160px;
    padding-bottom: 160px; } }

.block-journey {
  background: #2A3E65;
  width: 100%;
  overflow: hidden;
  padding-top: 80px;
  padding-bottom: 80px;
  border-radius: 30px 30px 0 0;
  &__title {
    color: #FFF;
    margin-bottom: 80px; }
  &__slider {
    max-width: 536px;
    margin: 0 auto;
    .item {
      padding-top: 70%;
      position: relative;
      &__img {
        z-index: 1;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        img {
          display: block;
          width: 100%;
          height: auto; } }
      &__year {
        z-index: 2;
        position: absolute;
        top: -23px;
        left: calc(50% - 60px);
        font-size: 26px;
        line-height: 46px;
        font-weight: 600;
        color: #FFF;
        padding: 0 24px;
        width: 120px;
        border-radius: 23px;
        text-align: center; }
      &__desc {
        z-index: 2;
        position: absolute;
        width: 70%;
        left: 15%;
        bottom: 0;
        padding: 0 0 32px;
        text-align: center; } }
    .owl-stage-outer {
      overflow: visible; }
    .owl-nav {
      button.owl-next {
        right: 12px; }
      button.owl-prev {
        left: 12px; } }
    .owl-dots {
      display: none; }
    .owl-item {
      z-index: 1;
      .item {
        transition: all .2s linear;
        -webkit-transform: perspective(600px) rotateY(20deg); }
      &.active {
        z-index: 2;
        .item {
          transform: none; } }
      &.active + .owl-item {
        z-index: 1;
        .item {
          -webkit-transform: perspective(400px) rotateY(-20deg); } }
      &.transition {
        .item {
          transform: none !important; } } } }
  &__slider-nav {
    max-width: 536px;
    margin: 32px auto 0;
    ul {
      display: block;
      text-align: center;
      background: #FFF;
      border-radius: 18px;
      font-size: 0;
      line-height: 0;
      padding: 4px 0;
      li {
        position: relative;
        display: inline-block;
        margin: 0 8.5%;
        cursor: pointer;
        img,
        img.color {
          position: relative;
          z-index: 2;
          display: block;
          width: 60px;
          height: 60px; }
        img.color {
          display: none; }
        &:before {
          content: "";
          z-index: 1;
          display: block;
          width: 200%;
          position: absolute;
          top: 50%;
          left: 80%;
          border-top: 2px dashed #A8A8A8; }
        &:first-child {
          margin-left: 0; }
        &:last-child {
          margin-right: 0;
          &:before {
            display: none; } }
        &:hover,
        &.active {
          img {
            display: none; }
          img.color {
            display: block; } } } } } }

// LOGIN
.login-page {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #64F4A3;
  &--klien {
    background: #F2785C; }
  &--talenta {
    background: #F26FAC; }
  &__type {
    max-width: 340px;
    margin: 0 auto;
    .title {
      font-size: 34px;
      line-height: 1.5em;
      text-align: center;
      margin-bottom: 48px; }
    .btn-box {
      flex-direction: column;
      text-align: center;
      width: 100%;
      span {
        display: block;
        margin-bottom: 24px; }
      .btn {
        width: 190px;
        border-radius: 8px;
        padding: 12px;
        img {
          display: block;
          height: 14px;
          width: auto;
          margin: 0 auto; }
        .img-w {
          display: none; }
        &+ .btn {
          margin-top: 24px; }
        &:hover {
          img {
            display: none; }
          .img-w {
            display: block; } }
        &-klien {
          border: 1px solid #F73824;
          &:hover {
            background: #F73824; } }
        &-talenta {
          border: 1px solid #F02784;
          &:hover {
            background: #F02784; } } } }
    .login-cta {
      text-align: center;
      margin-top: 80px;
      font-size: 14px;
      line-height: 1.5em; } }
  &__form {
    max-width: 310px;
    margin: 0 auto;
    align-self: flex-start;
    .title {
      font-size: 22px;
      line-height: 1.5em;
      margin-bottom: 32px;
      text-align: center; }
    .form-group {
      margin-bottom: 24px; }
    .form-action {
      span.or,
      span.forgot {
        display: block;
        font-size: 12px;
        line-height: 1.5em;
        padding: 12px;
        text-align: center; }
      .btn {
        font-size: 14px; } }
    label {
      color: #000;
      font-size: 12px;
      line-height: 1.5em;
      margin-bottom: 6px; }
    .form-control {
      border: 0;
      background: #F6F6F6; }
    .login-cta {
      text-align: center;
      margin-top: 40px;
      font-size: 14px;
      line-height: 1.5em; } }
  &__img {
    img {
      display: block;
      height: 580px;
      width: 580px; } }
  &__wrap {
    display: flex;
    position: relative;
    align-items: stretch; }
  &__left {
    z-index: 2;
    position: relative;
    display: block;
    width: 580px; }
  &__right {
    z-index: 1;
    position: relative;
    background: #FFF;
    margin-left: -30px;
    border-radius: 0 30px 30px 0;
    display: flex;
    align-items: flex-end;
    padding: 64px 40px 40px 70px;
    width: calc(100% - 550px);
    .back {
      display: block;
      position: absolute;
      top: 30px;
      left: 60px;
      cursor: pointer;
      opacity: 1;
      transition: all .3s ease;
      img {
        display: block;
        width: 24px;
        height: 24px; }
      &:hover {
        opacity: 0.5; } }
    .close {
      display: block;
      position: absolute;
      top: 30px;
      right: 30px;
      cursor: pointer;
      opacity: 1;
      transition: all .3s ease;
      img {
        display: block;
        width: 15px;
        height: 15px; }
      &:hover {
        opacity: 0.5; } } } }

// FOOTER CTA
.block-cta {
  position: relative;
  z-index: 1;
  padding-top: 42px;
  padding-bottom: 42px;
  background: #F6F6F6;
  border-radius: 30px 30px 0 0;
  margin-top: 80px;
  &:before {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 50px;
    top: 100%;
    left: 0;
    background: #F6F6F6; }
  &__title {
    font-size: 16px;
    line-height: 1.5em;
    margin-bottom: 0;
    color: $primary; }
  &__form {
    position: relative;
    .form-control {
      height: 48px;
      padding: 14px 96px 14px 24px;
      border: 0;
      border-radius: 10px;
      background: #FFF;
      &:focus {
        box-shadow: 0 0 0 2px rgba(28, 234, 111, 0.25); } }
    .btn {
      display: block;
      position: absolute;
      width: 70px;
      height: 36px;
      top: 6px;
      right: 6px;
      padding: 0 12px; } } }
